.profile {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  padding: 20px;
  text-align: center;

  width: 300px;
  height: 400px;
  border-radius: 15px;
  box-shadow: 10px 10px 10px -5px rgba(0, 0, 0, 0.3);
  background-color: rgb(244, 240, 240);
}

.avatar {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  box-shadow: 0 0 10px 5px rgba(0, 0, 0, 0.3);
  margin-bottom: 20px;
}

.name {
  font-size: 20px;
  font-weight: bold;
  margin-bottom: 10px;
}

.tag {
  color: red;
  margin-bottom: 10px;
  font-size: 15px;
}

.location {
  font-size: 18px;
  margin-bottom: 50px;
}

.stats {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 20px;
}

.item {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 8px;
  width: 80px;
  height: 80px;
  border-radius: 10px;
  background-color: rgb(217, 214, 214);
}

.label {
  font-size: 15px;
  color: black;
  opacity: 0.7;
}

.quantity {
  font-size: 20px;
  color: black;
  font-weight: bold;
}
